.projects-container {
    min-height: 100vh;
    background-image: radial-gradient(#c75346, #9f5148);
}
.projects-container h2 {
    text-align: center;
    padding: 50px;
    font-size: 3rem;
    color: white;
    text-shadow: 3px 3px 0 rgba(0,0,0,0.1);
}
.card-container {
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    align-content: center;
    max-width: 1200px;
    margin: 0 auto;
}

.project-card {
    background-color: white;
    width: 300px;
    height: 300px;
    border-radius: 10px;
    margin: 30px;
    position: relative;
    overflow: hidden;
    box-shadow: 10px 10px 10px rgba(0,0,0,0.4);
}

.project-card img {
    height: 300px;
}

.project-info{
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    text-align: center;
    background-color: rgba(255,255,255,0.85);
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 25px;
    transition: all 0.5s ease;
    z-index: 99;
}

.project-info:hover {
    opacity: 1;
}

.project-title {
    font-size: 1.3rem;
    width: 100%;
}
.project-desc {
    width: 100%;
}

.project-tech {

    font-size: 0.8rem;
    font-style: italic;

}

.repo-link {
    all: unset;
    cursor: pointer;
    font-weight: bold;
    text-decoration: underline;
    width: 300px;
}