.education-container {
    height: 100vh;
    overflow: auto;
}

.education-container {
    display: flex;
}

.education-left{
    flex: 60%;
    background-image: radial-gradient(#a9f4f0, #60a09d);
}

.education-right {
    flex: 40%;
    overflow: hidden;
    position: relative;
}

.education-left h2{
    text-align: center;
    padding: 40px;
    color: var(--red);
    font-size: 3rem;
    text-shadow: 4px 4px 1px rgba(0,0,0,0.05);
}

tr:nth-child(2n), thead{
    background-color: rgba(0,0,0,0.1)
}

table {
    border-collapse: collapse;
    width: 90%;
    margin: 0 auto;
    background-image: linear-gradient(rgba(0,0,0,0.05), rgba(0,0,0,0.2));
    
}

th {
    text-align: left;
}

td, th {
    padding: 8px;
    font-size: 0.9rem;
}

thead {
    border-bottom: 1px solid black;
}

.school img {
    height: 70px;
    margin: 15px 0;
}
.school{
    display: flex;
    width: 400px;
    margin: 0 auto 20px;
    justify-content: center;
}
.school-info {
    padding: 0 10px;
    margin: 20px 0;
}
.education-right img{
    object-fit:cover;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@media only screen and (max-width: 1448px) {
    .education-right{
        display: none;
    }
    table {
        width: 80%;
    }
    tr, th {
        font-size: 0.9rem;
    }

}

@media only screen and (max-width: 868px) {
    table {
        width: 96%;
    }
    tr, th {
        font-size: 0.8rem;
    }
    .school {
        width: 350px;
    }
    .school img {
        height: 50px;
    }
    .school h3 {
        font-size: 1rem;
    }
    .school p {
        font-size: 0.8rem;
    }
    .education-left h2{
        padding: 20px;
        font-size: 2rem;
    }
}

@media only screen and (max-width: 520px) {
    td{
        font-size: 13px;
    }
}

@media only screen and (max-width: 450px) {
    .school {
        width: auto;
        
    }
    .school-info {
        padding: 0 0 0 15px;
        margin: 0;
    }
    .school img {
        margin: 0;
    }
    .school h3 {
        font-size: 14px;
    }
    td {
        font-size: 11px;
    }
    .education-left h2 {
        font-size: 2rem;
    }
}