.about-container {
    min-height: 100vh;
    /* overflow: hidden; */
    position: relative;

}

.about-content{
    margin: 0 auto;
    height: 100vh;
    min-height: 720px;
    background-color: black;
    display: flex;
    flex-direction:row-reverse;
    background-color: white;
    overflow: hidden;
}

.about-left{
    position: relative;
    overflow: hidden;
    background-image: radial-gradient(#a9f4f0, #60a09d);
    width: 40%;
}

.about-right {
    background-image: radial-gradient(#c75346, #9f5148);
    width: 60%;
    position: relative;
    overflow: hidden;
    
}

.about-section{
    position: absolute;
    height: 200px;
    width: 450px;
    background-color: white;
    box-shadow: 10px 10px 0px rgba(0,0,0,0.3);
    padding: 15px 25px 15px 25px;
    color: #820d03;
    font-weight: 500;
    font-size: 1.1rem;
    line-height: 42px;
}

.about-first {
    left: -8px;
    top: 15%;
}

.about-second {
    right: -8px;
    top: 45%;
}

.about-third {
    width: 186px;
    height: 62px;
    left: -8px;
    top: 76%;
}

.about-container strong {
    font-weight: 700;
}

.icon-link a {
    font-size: 2rem;
    
    transition: all 0.3 ease;
    -webkit-transition: all 0.3 ease;
}

.linkedin a{
    color: rgb(28, 96, 223);
}

.github a {
    color: rgb(138, 17, 138);
    margin: 0 20px;
}

.instagram a {
    color: black;
}

.about-title{
    margin: 0 auto;
    text-align: center;
    font-size: 3rem;
    padding: 50px 0;
    color: white;
    text-shadow: 3px 3px 0 rgba(0,0,0,0.1);
}

@media only screen and (max-width: 1250px) { 
    .about-content {
        background-image: radial-gradient(#c75346, #9f5148);
        width: 100%;
        border-radius: 0;
    }
    .about-left {
        width: 100%;
    }
    .about-right {
        width: 100%;
        background-image: none;
    }

}

@media only screen and (max-width: 934px) { 
    .about-left {
        display: none;
    }
    .about-first, .about-third {
        left: -8px;
    }

}

@media only screen and (max-width: 450px) { 

    .about-section {
        width: 270px;
        line-height: 20px;
        font-size: 1rem;
        height: auto;
        
    }
    .about-third {
        width: 190px;
    }
    .about-title {
        padding: 10px 0;
    }
}

